.collection-item {
  width: 280px;
  display: flex;
  flex-direction: column;
  height: 360px;
  align-items: center;
  position: relative;

  .card-container {
    display: flex;
    flex-direction: column;
    background-color: #f1f1e8;
    border: 1px solid rgb(204, 204, 204);
    border-radius: 5px;
    padding: 15px;
    // cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
    height: 100%;
    width: 100%;

    &:hover {
      transform: scale(1.05);
    }

    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      // margin-bottom: 5px;
    }

    .custom-button {
      width: 80%;
      opacity: 0.7;
      position: absolute;
      top: 220px;
      display: none;
      margin: 0px 0px 0px 15px;
    }

    &:hover {
      .image {
        opacity: 0.8;
      }

      .custom-button {
        opacity: 0.85;
        display: flex;
      }
    }

    .collection-footer {
      width: 100%;
      height: 20%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;

      .name {
        width: 82%;
        height: 50px;
        margin: 5px 0px;
      }

      .price {
        width: 18%;
        text-align: right;
        color: green;
        margin: 5px 0px;
      }
    }
  }
}
