.collection-preview {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  width: 100%;
  min-height: 90vh;
  align-items: center;
  margin: 50px auto 0;

  .title {
    font-size: 1.1rem;
    margin-bottom: 15px;
    margin-top: 70px;
    padding: 10px;
    border-bottom: 1px solid rgb(223, 221, 221);
  }

  .categories-preview {
    width: 80%;
    max-width: 960px;
  }

  .preview {
    width: 100%;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-gap: 25px;
    justify-content: space-between;
    // flex-wrap: wrap;
    // justify-content: space-between;
  }
}
