.checkout-item {
  width: 100%;
  display: flex;
  min-height: 100px;
  border-bottom: 1px solid darkgrey;
  padding: 10px 0px 10px 0px;
  font-size: 20px;
  align-items: center;

  .image-container {
    width: 23%;
    padding: 0px 15px 0px 15px;
    background-color: white;
    margin-right: 10px;
    border-radius: 5px;
    border: 1 solid rgb(209, 209, 209);

    img {
      width: 100%;
      height: 100%;
    }
  }
  .name,
  .quantity,
  .price {
    width: 23%;
  }

  .quantity {
    display: flex;

    .arrow {
      cursor: pointer;
      color: red;
    }

    .value {
      margin: 2px 10px 0px 10px;
      color: green;
    }
  }

  .remove-button {
    padding-left: 12px;
    cursor: pointer;
    color: red;
    font-weight: bold;
  }
}
